* {
  box-sizing: border-box;
}

body {
  color: #1a1a1a;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Inter, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, sans-serif;
  line-height: 1.6;
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-track {
  background: #ffffff1a;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: #ffffff4d padding-box content-box;
  border: 2px solid #0000;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #ffffff80 padding-box content-box;
}

.App {
  min-height: 100vh;
  padding: 20px;
  position: relative;
}

.App:before {
  content: "";
  pointer-events: none;
  z-index: -1;
  background: radial-gradient(circle at 20% 80%, #7877c64d 0%, #0000 50%), radial-gradient(circle at 80% 20%, #ff77c64d 0%, #0000 50%), radial-gradient(circle at 40% 40%, #78dbff33 0%, #0000 50%);
  position: fixed;
  inset: 0;
}

.app-header {
  text-align: center;
  color: #fff;
  z-index: 1;
  margin-bottom: 40px;
  position: relative;
}

.header-content {
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-bottom: 15px;
  display: flex;
}

.timeline-icon {
  filter: drop-shadow(0 4px 8px #0003);
  width: 60px;
  height: 40px;
  transition: transform .3s;
}

.timeline-icon:hover {
  transform: scale(1.1);
}

.app-header h1 {
  -webkit-text-fill-color: transparent;
  text-shadow: 0 4px 8px #0000001a;
  letter-spacing: -.02em;
  background: linear-gradient(45deg, #fff, #f0f0f0) text;
  margin: 0;
  font-size: 3.5rem;
  font-weight: 800;
}

.app-header p {
  opacity: .9;
  text-shadow: 0 2px 4px #0000001a;
  margin: 0;
  font-size: 1.3rem;
  font-weight: 400;
}

.app-main {
  backdrop-filter: blur(20px);
  background: #fffffff2;
  border: 1px solid #fff3;
  border-radius: 24px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 25px 50px #00000026, 0 0 0 1px #ffffff1a;
}

.app-main:before {
  content: "";
  background: linear-gradient(90deg, #0000, #ffffff80, #0000);
  height: 1px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.text-center {
  text-align: center;
}

.mb-20 {
  margin-bottom: 20px;
}

.mt-20 {
  margin-top: 20px;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }
}

.animate-fade-in-up {
  animation: .6s ease-out fadeInUp;
}

.animate-slide-in-left {
  animation: .6s ease-out slideInLeft;
}

.animate-pulse {
  animation: 2s infinite pulse;
}

@media (width <= 768px) {
  .App {
    padding: 15px;
  }

  .header-content {
    gap: 15px;
  }

  .timeline-icon {
    width: 50px;
    height: 35px;
  }

  .app-header h1 {
    font-size: 2.5rem;
  }

  .app-header p {
    font-size: 1.1rem;
  }

  .app-main {
    border-radius: 20px;
  }
}

@media (width <= 480px) {
  .App {
    padding: 10px;
  }

  .header-content {
    flex-direction: column;
    gap: 12px;
  }

  .timeline-icon {
    width: 45px;
    height: 30px;
  }

  .app-header h1 {
    font-size: 2rem;
  }

  .app-header p {
    font-size: 1rem;
  }
}
